@import "variables";
.cytoscape__div {
  background-color: $base-cy-background-color;
  width: 100%;
  @media only screen and (max-width: 415px) {
    height: 1500px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-bottom: 20px;
  }
  @media only screen and (min-width: 415px) and (max-width: 769px) {
    height: 1500px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-bottom: 20px;
  }
  @media only screen and (min-width: 769px) {
    border-bottom: 1px solid lightgray;
    border-top: 1px solid lightgray;
    height: calc(100vh - 280px - 65px);
  }
}

.cytoscape-calc__div {
  background-color: $base-cy-background-color;
  width: 100%;
  @media only screen and (max-width: 415px) {
    height: 1500px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-bottom: 20px;
  }
  @media only screen and (min-width: 415px) and (max-width: 769px) {
    height: 1500px;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-bottom: 20px;
  }
  @media only screen and (min-width: 769px) {
    height: calc(100vh - 280px - 75px - 65px);
    border-bottom: 1px solid lightgray;
    border-top: 1px solid lightgray;
  }
}



