.loading-spinner{
  left: 50%;
  top: 5%;
  position: absolute;
  z-index: 2;
}

.scrape-controls__div{
  .line-high{
    line-height: 44px;
  }

  @media only screen and (max-width: 415px) {
    display: flex;
    align-content: stretch;
    flex-direction: column;
    margin: 15px 0;
    .gen__button{
      button {
        width: 100%;
        margin-top: 10px;
      }
    }
  }
  @media only screen and (min-width: 415px) and (max-width: 769px) {
    display: flex;
    align-content: stretch;
    flex-wrap: wrap;
    margin: 15px 0;
    .gen__button{
      button {
        width: 100%;
        margin-top: 10px;
      }
    }
    .posts-slider{
      flex-grow: 1;
    }
  }
  @media only screen and (min-width: 769px) {
    display: flex;
    align-items: baseline;
    width: 80vw;
    margin: 20px 10px;
    .posts-slider{
      flex-grow: 1;
    }
  }
}


